.navbar-wrapper {
    position: absolute;
    z-index: 999;
    width: 100%;
}

.navbar {
    margin: 2%;
    color: aliceblue;
    display: flex;
    flex-direction: row;
    border-top: 1px solid rgba(178, 190, 195, 1);
    border-left: 1px solid rgba(178, 190, 195, 1);
    border-right: 1px solid rgba(178, 190, 195, 1);
}

.navbar div {
    width: 50%;
    padding-right: 3%;
    padding-left: 3%;
}

.logo {
    width: 250px;
    margin-top: 3%;
}

.list {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
}

.list li {
    display: inline;
    margin-top: 3%;
}

li a {
    padding: 16px;
    cursor: pointer;
    transition: 0.1s linear;
    font-size: 1rem;
}

li a:hover {
    font-weight: bold;
    /* font-size: 1.1rem; */
}

.footer-wrapper {
    position: absolute;
    width: 100%;
    z-index: 999;
    bottom: 0;
}

.footer {
    margin: 2%;
    color: aliceblue;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid rgba(178, 190, 195, 1);
    border-left: 1px solid rgba(178, 190, 195, 1);
    border-right: 1px solid rgba(178, 190, 195, 1);
}

.footer div {
    padding: 1%;
    width: 50%;
}

#carousel figure h2 {
    font-size: 5vw;
    line-height: 4vw;
    margin: 0;
}

#carousel figure img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#carousel figure hr {
    display: block;
    margin: 8px 0;
}

#carousel figure ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.5vw;
}

#carousel figure ul li {
    padding-top: 2px;
}

#carousel figure button {
    position: absolute;
    bottom: 10%;
    left: 0;
    right: 0;
    margin: auto;
    display: block;
    width: 30%;
    height: 6vh;
    font-size: 1.5vw;
}

h1 {
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 5vh;
    left: 0;
    right: 0;
    margin: auto;
}

button.controls {
    position: absolute;
    z-index: 999;
    padding: 10px 15px;
    background: tomato;
    cursor: pointer;
}

button.controls:nth-of-type(1) {
    top: 0;
    left: 0;
}

button.controls:nth-of-type(2) {
    top: 0;
    right: 0;
}

